<script>
import axios from "axios";
import * as XLSX from 'xlsx';
import { saveAs } from 'file-saver';
export default {
  data(){
    return{
      all_users:[],
      statistiks:[],
      statsExel: [],
      json_fields: {
        'FOYDALANUVCHILAR': 'name',
        'JAMI (OʻQUVCHILAR)': 'city',
        'SHUNDAN': 'phone.mobile',
        'JAMI SO’ROVNOMADA QATNASHGANLAR': 'city',
        'SHUNDAN SOROVNOMADA QATNASHGANLAR': 'phone.mobile',

      },
      json_data: [
        {
          'name': 'Tony Peña',
          'city': 'New York',
          'country': 'United States',
          'birthdate': '1978-03-15',
          'phone': {
            'mobile': '1-541-754-3010',
            'landline': '(541) 754-3010'
          }
        },
      ],
      json_meta: [
        [
          {
            'key': 'charset',
            'value': 'utf-8'
          }
        ]
      ],
    }
  },
  mounted() {

    axios.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers4/`, {
      headers: {
        'Authorization': `Bearer ${localStorage.getItem('token')}`,
        'Accept-Language': 'uz'
      }
    })
      .then(res=>{
        this.statistiks=res.data.results.statistics

      })
  },
  methods:{
    exportTableToExcel() {
      const table = document.getElementById('my-table');
      const worksheet = XLSX.utils.table_to_sheet(table);
      const workbook = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');

      const wbout = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });

      try {
        saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'table_data.xlsx');
      } catch (e) {
        if (typeof console !== 'undefined') console.log(e, wbout);
      }
    }
  }
}
</script>
